.wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-top: 8px;
    border-radius: 8px;
    max-height: min(
        calc(100vh - 96px - var(--default-layout-header-hight)),
        734px
    );
    min-height: 100px;
    background-color: #fff;
    box-shadow: rgb(0 0 0 / 12%) 0px 2px 12px;
}
