.wrapper {
    display: flex;
    align-items: center;
    padding: 6px 16px;
    cursor: pointer;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

.avatar {
    margin-right: 12px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.info {
    flex: 1;
}

.name {
    font-size: 1.6rem;
    font-weight: 500;
}

.check {
    font-size: 1.4rem;
    margin-left: 6px;
    color: rgb(32, 213, 236);
}

.user-name {
    font-size: 1.4rem;
    color: rgba(22, 24, 35, 0.5);
    line-height: 1.3;
}
